	module.exports = {"data":{"markdownRemark":{"html":"<h2>Pair Programming</h2>\n<p>Extreme Programming (XP) recommends that all production code be written by two programmers working at the same time at the same computer, taking turns on the keyboard. Several academic studies have demonstrated that pair programming results in fewer bugs and more maintainable software. Pair programming can also help spread knowledge within the team, contributing to a larger <a href='http://deviq.com/bus-factor/' target='_blank' rel='nofollow'>Bus Factor</a> and helping to promote <a href='http://deviq.com/collective-code-ownership/' target='_blank' rel='nofollow'>Collective Code Ownership</a>.</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href='http://bit.ly/PS-PairProgramming' target='_blank' rel='nofollow'>Pair Programming Fundamentals (training course)</a></li>\n<li><a href='https://www.versionone.com/agile-101/agile-software-programming-best-practices/pair-programming/' target='_blank' rel='nofollow'>Agile Software Programming Best Practices</a></li>\n</ul>","fields":{"slug":"/agile/pair-programming/"},"frontmatter":{"title":"Pair Programming"}}},"pathContext":{"slug":"/agile/pair-programming/"}}

